import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { RouterModule } from '@angular/router';
import { CapCheckComponent } from './capcheck.component';
import { WebcamModule } from 'ngx-webcam';
import { ImageCropperModule } from 'ngx-image-cropper';
import { TextareaAutosizeModule } from 'ngx-textarea-autosize';
import { CapCheckResultComponent } from './capcheckresult/capcheckresult.component';
import { CapcheckListComponent } from './capchecklist/capchecklist.component';
import { NgxPageScrollCoreModule } from 'ngx-page-scroll-core';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    WebcamModule,
    ImageCropperModule,
    TextareaAutosizeModule,
    NgxPageScrollCoreModule,
    RouterModule.forChild([
      {
        path: '',
        component: CapCheckComponent
      },
      {
        path: 'result',
        component: CapCheckResultComponent
      },
      {
        path: 'list',
        component: CapcheckListComponent
      }
    ])
  ],
  declarations: [CapCheckComponent, CapCheckResultComponent, CapcheckListComponent]
})
export class CapCheckComponentModule {}
