import { Component, OnInit, ViewChild, QueryList, ViewChildren, ElementRef, Inject, EventEmitter, AfterViewChecked, AfterContentChecked } from '@angular/core';
import {Subject, Observable, of} from 'rxjs';
import {WebcamImage, WebcamInitError, WebcamUtil} from 'ngx-webcam';
import { createWorker } from 'tesseract.js';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import { CommonService } from '../services/common.service';
import { LoadingController, NavController, AngularDelegate, IonContent, ActionSheetController, IonButton } from '@ionic/angular';
import { Mark6Service } from '../services/mark6.service';
import { ActivatedRoute, Router } from '@angular/router';
import { DataVal } from '../models/DataVal';
import { MarkSixResult, M6CheckResult, copyInstance, clone, isNumberVal, hasDuplicates } from '../models/common.model';
import { ViewportScroller, DOCUMENT } from '@angular/common';
import { PageScrollService } from 'ngx-page-scroll-core';


@Component({
  selector: 'app-capcheck',
  templateUrl: './capcheck.component.html',
  styleUrls: ['./capcheck.component.scss']
})
export class CapCheckComponent implements OnInit, AfterViewChecked {

  @ViewChildren('inputNums')inputNums: QueryList<ElementRef>;
  @ViewChild('fileButtonClick', {static: true})fileButton: ElementRef<HTMLElement>;
  @ViewChild(IonContent, { static: true }) content: IonContent;

  constructor(private commonService: CommonService, private mark6Service: Mark6Service,
    private _router: Router, private _activeRoute: ActivatedRoute,
    private navCtrl: NavController, private loadingController: LoadingController,
    private dataVal: DataVal, private pageScrollService: PageScrollService,
    private actionCtrl: ActionSheetController,
    @Inject(DOCUMENT) private document: any) { }

    private MAX_NUM = 49;

  public screenWidth = window.innerWidth;

  public resultList: Array<String> = new Array<String>();
  public result = '-';
  public result1 = '-';
  public showError: false;
  public analysisClick = false;
  public subject: any;

  // toggle webcam on/off
  public showWebcam = true;
  public allowCameraSwitch = true;
  public multipleWebcamsAvailable = false;
  public deviceId: string;
  public videoOptions: MediaTrackConstraints = {
    // width: {ideal: 1024},
    // height: {ideal: 576}
  };
  public errors: WebcamInitError[] = [];

  // latest snapshot
  public webcamImage: WebcamImage = null;
  public rotateVal: number = 0;

  // webcam snapshot trigger
  private trigger: Subject<void> = new Subject<void>();
  // switch to next / previous / specific webcam; true/false: forward/backwards, string: deviceId
  private nextWebcam: Subject<boolean|string> = new Subject<boolean|string>();

  public ngOnInit(): void {
    WebcamUtil.getAvailableVideoInputs()
      .then((mediaDevices: MediaDeviceInfo[]) => {
        this.multipleWebcamsAvailable = mediaDevices && mediaDevices.length > 1;
      });
  }

  ionViewDidEnter() {
    this.commonService.prepareInterstitialAds();
  }

  ngAfterViewChecked() {

  }


  public triggerSnapshot(): void {
    this.trigger.next();
  }

  public toggleWebcam(): void {
    this.showWebcam = !this.showWebcam;
  }

  public handleInitError(error: WebcamInitError): void {
    this.errors.push(error);

    if (!this.showError) {
      this.commonService.showAlert('ERROR', 'iPhone(iOS) 用户請使用 Safari 開啟!');
    }
  }

  public showNextWebcam(directionOrDeviceId: boolean|string): void {
    // true => move forward through devices
    // false => move backwards through devices
    // string => move to device with given deviceId
    this.nextWebcam.next(directionOrDeviceId);
  }

  public handleImage(webcamImage: WebcamImage): void {
    console.info('received webcam image', webcamImage);
    this.webcamImage = webcamImage;
    this.theCamImage = this.webcamImage.imageAsDataUrl;
  }

  public cameraWasSwitched(deviceId: string): void {
    console.log('active device: ' + deviceId);
    this.deviceId = deviceId;
  }

  public get triggerObservable(): Observable<void> {
    return this.trigger.asObservable();
  }

  public get nextWebcamObservable(): Observable<boolean|string> {
    return this.nextWebcam.asObservable();
  }

  takePhoto() {
    this.result = '-';
    this.croppedImage = null;
    this.trigger.next();
  }


  check() {
    this.resultList.length = 0;
    this.resultList = [];
    if (this.croppedImage) {
      this.doOCR();
    } else {
      this.commonService.showAlert('Error', '請先 [選取彩票]，然後裁剪彩票至只有投注號碼！');
    }
  }

  doRotate() {
    this.rotateVal = this.rotateVal + 1;

    if (this.rotateVal == 4) {
      this.rotateVal = 0;
    }
  }

  reset() {
    this.result = '-';
    this.croppedImage = null;
    this.resultList.length = 0;
    this.resultList = [];
    this.analysisClick = false;
  }


  theCamImage: any;
  imageChangedEvent: any = '';
  croppedImage: any = '';
 
  fileChangeEvent(event: any): void {
    this.reset();
    this.imageChangedEvent = event;
  }

    imageCropped(event: ImageCroppedEvent) {
        this.croppedImage = event.base64;
    }
    imageLoaded() {
      //console.log('cropper image loaded');
    }
    cropperReady() {
        // cropper ready
        //console.log('cropper ready');
    }
    loadImageFailed() {
        // show message
        //console.log('cropper loadImageFailed');
    }


    async doOCR() {

      const worker = createWorker({
        logger: m => console.log(m)
      });

      
      let loading = await this.loadingController.create({
        message: '號碼辨識中...',
        spinner: 'circles'
      });
  
      loading.present().then(() => {
        worker.load().then(() => {
          worker.loadLanguage('eng').then(() => {
            worker.initialize('eng').then(() => {
              worker.setParameters({
                  tessedit_char_whitelist: '0123456789+/',
              }).then(() => {
                worker.recognize(this.croppedImage).then((resultData) => {
                  //console.log(resultData.data.text);
                  this.result = resultData.data.text;
                  worker.terminate();
                  /*
                  this.doCheck(loading).subscribe((data) => {
                    console.log('CHECK RESULT: ' + JSON.stringify(data));
                    this.result1 = JSON.stringify(data);
                    loading.dismiss();
                  });
                  */

                 if (this.result != null && this.result.length > 0) {

                  const nums = this.result.split('/');

                  // tslint:disable-next-line: forin
                  for (const n in nums) {
                    const theNum = nums[n].replace(/(\r\n|\n|\r)/gm, '').replace(/\s/g, '');
                    this.resultList.push(theNum);
                  }
                }

                  loading.dismiss();

                  setTimeout(() => {this.content.scrollToBottom();}, 300);

                });
              });
            });
          });
        });
      }).finally(() => {

      });

  }

 


    async doCheck() {
      let finalResult: Array<M6CheckResult>;

      finalResult = new Array<M6CheckResult>();


      if (this.resultList.length !== 0) {
        let loading = await this.loadingController.create({
          message: '核對中...',
          spinner: 'circles'
        });

        loading.present().then(() => {

          this.mark6Service.getLast30().subscribe(result30 => {
            if (result30 != null && result30.length > 0) {


              let inUums: ElementRef[] = this.inputNums.toArray();

              this.resultList.length = 0;
              this.resultList = [];

              // tslint:disable-next-line: forin
              for(const inu in inUums) {
                this.resultList.push(inUums[inu].nativeElement.value);
              }


              //const nums = this.result.split('/');

              // tslint:disable-next-line: forin
              //for (const n in nums) {
              for (const n in this.resultList) {
                let bNoVal = Number(n) + 1;

                //const theNum = nums[n].replace(/(\r\n|\n|\r)/gm, '');
                const theNum = this.resultList[n].replace(/(\r\n|\n|\r)/gm, '');

                const bankerList = theNum.split('+');

                let selectedNumber = new Array<number>();

                // tslint:disable-next-line: forin
                for (const bNo in bankerList) {
                  // console.log(Number(bankerList[bNo]));

                  const theBNumber = Number(bankerList[bNo]);

                  if (theBNumber > this.MAX_NUM || theBNumber <= 0 || !isNumberVal(bankerList[bNo])) {
                    this.commonService.showAlert('ERROR', `第 ${bNoVal} 注含有錯誤號碼，請修正！`);
                    loading.dismiss();

                    finalResult.length = 0;
                    finalResult = [];

                    return;
                  } else {
                    selectedNumber.push(theBNumber);
                  }
                }

                if (selectedNumber.length < 6) {
                  this.commonService.showAlert('Error', `第 ${bNoVal} 注最少要有6個字，請修正！`);
                  loading.dismiss();
                  finalResult.length = 0;
                  finalResult = [];
                  return;
                }

                if (hasDuplicates(selectedNumber)) {
                  this.commonService.showAlert('Error', `第 ${bNoVal} 注含有重覆號碼，請修正！`);
                  loading.dismiss();
                  finalResult.length = 0;
                  finalResult = [];
                  return;
                }


                if (selectedNumber.length > this.MAX_NUM) {
                  this.commonService.showAlert('Error', `第 {bNoVal} 注不能多於 ${this.MAX_NUM} 個字，請修正！`);
                  loading.dismiss();
                  finalResult.length = 0;
                  finalResult = [];
                  return;
                }

                let iCheckResult = new M6CheckResult();
                iCheckResult.numbers = theNum;

                let matchedResults = new Array<MarkSixResult>();

                  for (const tmpResult30 of result30) {
                    let iResult: MarkSixResult = new MarkSixResult();
                    iResult = JSON.parse(JSON.stringify(tmpResult30));

                    let matchCount = 0;

                    for (const selNo of selectedNumber) {
                      if (selNo === iResult.number1) {
                        iResult.matched[0] = true;

                        matchCount++;
                      } else if (selNo === iResult.number2) {
                        iResult.matched[1] = true;

                        matchCount++;
                      } else if (selNo === iResult.number3) {
                        iResult.matched[2] = true;

                        matchCount++;
                      } else if (selNo === iResult.number4) {
                        iResult.matched[3] = true;

                        matchCount++;
                      } else if (selNo === iResult.number5) {
                        iResult.matched[4] = true;

                        matchCount++;
                      } else if (selNo === iResult.number6) {
                        iResult.matched[5] = true;

                        matchCount++;
                      }

                      if (selNo === iResult.numberX) {
                        iResult.matched[6] = true;
                        matchCount = matchCount + 0.5;
                      }

                    }

                    if (matchCount >= 3) {
                      matchedResults.push(iResult);
                    }

                  }

                  iCheckResult.matchedResults = matchedResults;

                  finalResult.push(iCheckResult);

                }

                this.result1 = JSON.stringify(finalResult);

                loading.dismiss();

                if (finalResult.length >= 1) {
                  this.dataVal.storage = finalResult;
                  this.navCtrl.navigateForward(['capcheck/result']);
                } else {
                  this.commonService.showAlert('Info', '閣下的彩票在最近30期沒有中獎');
                }
              } else {
                  //console.log('>>>> no result:::');
                  loading.dismiss();
                  finalResult.length = 0;
                  finalResult = [];

                  return;
              }
          }, error => {
              loading.dismiss();
              console.log(error);
              finalResult.length = 0;
              finalResult = [];

              return;
          });
        });
      } else {
         //console.log('ELSE::::' + JSON.stringify(finalResult));
         this.commonService.showAlert('Error', '請先 [選取彩票]，然後裁剪彩票至只有投注號碼！');
         return;
      }
    }


    openActionSheet() {
      this.actionCtrl.create({
        buttons: [
          {
            text: '選取彩票',
            icon: 'camera',
            handler: () => {
              const el: HTMLElement = document.getElementById('fileButtonClick') as HTMLElement;
              el.click();
            }
          },
          {
            text: '辨識號碼',
            icon: 'barcode',
            handler: () => {
              this.check();
            }
          },
          {
            text: '核對彩票',
            icon: 'contract',
            handler: () => {
              this.commonService.showInterstitialAds();
              this.doCheck();
            }
          },
          {
            text: '旋轉',
            icon: 'return-up-forward-outline',
            handler: () => {
              this.doRotate();
            }
          },
          {
            text: "取消",
            role: 'cancel'
          }
        ]
      }).then(ac => ac.present());
    }

}
