import { Component, OnInit } from '@angular/core';
import { DataVal } from 'src/app/models/DataVal';
import { Mark6Service } from 'src/app/services/mark6.service';
import { Router } from '@angular/router';
import { LoadingController, NavController } from '@ionic/angular';
import { CommonService } from 'src/app/services/common.service';
import { M6CheckResult, MarkSixResult } from 'src/app/models/common.model';

@Component({
  selector: 'app-capcheckresult',
  templateUrl: './capcheckresult.component.html',
  styleUrls: ['./capcheckresult.component.scss']
})
export class CapCheckResultComponent implements OnInit {

  public results: M6CheckResult[];

  constructor(private dataVal: DataVal,
    private commonService: CommonService, private m6service: Mark6Service, private _router: Router,
    private navCtrl: NavController, private loadingController: LoadingController) { }

  ngOnInit() {
    let tmpResults: M6CheckResult[] = this.dataVal.storage;

    this.results = [];

    // tslint:disable-next-line: forin
    for (const iResult in tmpResults) {
      let iRes = tmpResults[iResult];
      iRes.numbers = iRes.numbers.replace(/([+])/g, ' $1 ');

      this.results.push(iRes);
    }
  }

  showDetail(m6Result: M6CheckResult) {
    if (m6Result.matchedResults.length > 0) {
      this.dataVal.storage = m6Result;
      this.navCtrl.navigateForward(['capcheck/list']);
    }
  }

}
