import { Component, OnInit } from '@angular/core';
import { DataVal } from 'src/app/models/DataVal';
import { CommonService } from 'src/app/services/common.service';
import { Mark6Service } from 'src/app/services/mark6.service';
import { Router } from '@angular/router';
import { LoadingController, NavController } from '@ionic/angular';
import { MarkSixResult, M6CheckResult } from 'src/app/models/common.model';

@Component({
  selector: 'app-capchecklist',
  templateUrl: './capchecklist.component.html',
  styleUrls: ['./capchecklist.component.scss']
})
export class CapcheckListComponent implements OnInit {

  m6results: M6CheckResult;

  constructor(private dataVal: DataVal,
    private commonService: CommonService, private m6service: Mark6Service, private _router: Router,
    private navCtrl: NavController, private loadingController: LoadingController) { }

  ngOnInit() {
    this.m6results = this.dataVal.storage;

    if (this.m6results == null) {
      this.navCtrl.navigateRoot('/404');
    }
  }


  async showDetail(val) {
    // console.log(val);
    // this._router.navigate(['last30/detail' , val]);

    let loading = await this.loadingController.create({
      message: 'Loading',
      spinner: 'circles'
    });

    loading.present().then(() => {
      this.navCtrl.navigateForward(['last30/detail' , val]);
      loading.dismiss();
    });
  }

  toDate(val) {
    if (val != null) {
      return this.m6service.toDateStr(Number(val));
    } else {
      return '';
    }
    // return this.m6service.toDateStr(Number(val));
  }
}
